import { createRouter, createWebHistory } from 'vue-router'
import CaesarWaffle from "@/views/CaesarWaffle.vue";
import AboutView from "@/views/AboutView.vue";
import FantasyMonster from "@/views/FantasyMonster.vue";
import GrenadeView from "@/views/GrenadeView.vue";
import ArtOpensDoor from "@/views/ArtOpensDoor.vue";
import CityView from "@/views/CityView.vue";
import PlayTime from "@/views/PlayTime.vue";
import SketchesView from "@/views/SketchesView.vue";
import DragonView from "@/views/DragonView.vue";
import LandscapeView from "@/views/LandscapeView.vue";
const routes = [
  {
    path: '/:catchAll(.*)',
    component: AboutView
  },
  {
    path: '/',
    name: 'about',
    component: AboutView
  },
  {
    path: '/caesar-waffle',
    name: 'caesar-waffle',
    component: CaesarWaffle
  },
  {
    path: '/fantasy-monster',
    name: 'fantasy-monster',
    component: FantasyMonster
  },
  {
    path: '/grenade',
    name: 'grenade',
    component: GrenadeView
  },
  {
    path: '/art-opens-door',
    name: 'art-opens-door',
    component: ArtOpensDoor
  },
  {
    path: '/city',
    name: 'city',
    component: CityView
  },
  {
    path: '/play-time',
    name: 'play-time',
    component: PlayTime
  },
  {
    path: '/sketches',
    name: 'sketches',
    component: SketchesView
  },
  {
    path: '/dragon',
    name: 'dragon',
    component: DragonView
  },
  {
    path: '/landscapes',
    name: 'landscapes',
    component: LandscapeView
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition;
    } else {
      return { top: 0 };
    }
  }
})

const queryParams = new URLSearchParams(window.location.search);
const redirectPath = queryParams.get('redirect');
if (redirectPath) {
  router.replace(redirectPath);
}

export default router
